export {helpers}

const d3 = require('d3')

const helpers = (function () {

	let months = ['Ene', 'Feb', 'Mar', 'Abr', 'Mayo', 'Jun', 'Jul', 'Ago', 'Sep', 'Oct', 'Nov', 'Dic']; 

	function formatDate (date, months) {
		return date.getDate() + ' ' + months[date.getMonth()];
	}

	function filterRegionalData(data, region, type) {
	return data[region][type].map(el => { return { x: new Date(el.x), y: el.y } })
	}

	function filterNationalData(data, type) {
	console.log('filtering national data', data, type)
	return data.totals[type].map(el => { return { x: new Date(el.x), y: el.y } })
	}

	function defineBounds (width, height, padding) {
		return {
			top: padding[0],
			right: width - padding[1] - padding[3],
			bottom: height - padding[2],
			left: padding[3],
		}
	}

	function createZeroValues (dataset) {
		return dataset.data.map(function(el){ return {x: el.x, y: 0} });
	}

	function setNoFill (object) {
		object.style('fill', 'none');
	}

	function setStrokeColor (object, color) {
		object.style('stroke', color);
	}

	function setStrokeWidth (object, number) {
		object.style('stroke-width', number);
	}

	function setDashArray (object, value) {
		object.style('stroke-dasharray', value);
	}

	function transitionLine (line, chart, dataset, duration) {
		line.transition().duration(duration)
		.attr('d', chart.lineGen(dataset.data));
	}

	function transitionTrendline (trendline, chart, dataset, duration) {
		trendline.transition().duration(duration)
		.attr('d', chart.trendGen(dataset.data));
	}

	function transitionPoints (points, chart, dataset, duration) {
		points.transition().duration(duration)
		.attr('cx', d => chart.xScale(d.x))
		.attr('cy', d => chart.yScale(d.y))
		.attr('r', dataset.radius ? dataset.radius : 3);
	}

	function drawLine (chart, dataset) {
		let line = chart.svg.append('g').append('path')
		.attr('d', chart.lineGen(createZeroValues(dataset)))
		.attr('class', 'line')
		.attr('id', chart.id + '-line-' + (chart.dataset.length));

		setStrokeColor(line, dataset.color);
		setNoFill(line);

		dataset.line = line;
		dataset.lineId = chart.id + '-line-' + chart.dataset.length;

		transitionLine(line, chart, dataset, 700);
	}

	function drawTrendline (chart, dataset) {
		if (dataset.trendline) {

			let trendline = chart.svg.append('g')
			.append('path')
			.attr('d', chart.trendGen(createZeroValues(dataset)))
			.attr('class', 'trendline')
			.attr('id', chart.id + '-trendline-' + chart.dataset.length);

			setNoFill(trendline);
			setStrokeColor(trendline, dataset.color);
			setDashArray(trendline, 3);
			
			dataset.trendline = trendline;

			transitionTrendline(trendline, chart, dataset, 700);

		}
	}

	function drawPoints (chart, dataset) {
		if (dataset.pointsOn) {

			let points = chart.svg.append('g')
			.attr('id', dataset.lineId + '-points')
			.selectAll('circle')
			.data(dataset.data).enter()
			.append('circle')
			.attr('cx', d => chart.xScale(d.x))
			.attr('cy', chart.drawingBounds.bottom)
			.attr('r', 0)
			/*eslint-disable no-unused-vars*/
			.attr('id', (d, i) => dataset.lineId + '-point-' + i);
			/*eslint-enable no-unused-vars*/

			setStrokeColor(points, dataset.color);
			setNoFill(points);
			setStrokeWidth(points, 2);

			dataset.points = points;

			transitionPoints(points, chart, dataset, 700);

		}
	}

	

	function createGridSpacing (chart, dataset_index) {
		if (chart.dataset.length > 0 && chart.dataset[dataset_index]){
			return ((chart.drawingBounds.right - chart.drawingBounds.left) / chart.dataset[dataset_index].data.length) / 2;
		} else {
			return undefined;
		}
	}

	function createLineGen (curveType, chart) {
		return d3.line().curve(curveType).x(d => chart.xScale(d.x)).y(d => chart.yScale(d.y));
	}

	function setLabelVal(chart, dataset_index, data_index) {
		let currDataset = chart.dataset[dataset_index];
		data_index = data_index > currDataset.xData.length - 1 ? currDataset.xData.length - 1 : data_index;
		currDataset.labelVal = currDataset.yData[data_index];

		if (dataset_index == chart.winnerX) {
			let formattedDate = formatDate(currDataset.xData[data_index], months)
			if (chart.currX != formattedDate) {
				chart.currX = formattedDate;
			}
		}
	}

	function styleHoverPoint(chart, motion, dataset_index, point_index, {color='black', radius=3} = {}) {
		if (chart.dataset[dataset_index].points){
			d3.select(chart.dataset[dataset_index].points.nodes()[point_index])
			.style('fill', motion == 'enter' ? color : 'none')
			.style('border-color', 'red')
			.attr('r', radius)
		}
	}


	//*** Refactored to return as opposed to mutate */	
	function buildNewDatasetRefac(dataset, chart) {

		console.log('Dataset being passed to buildnewdataset', dataset)

		let currDataset = Object.assign({}, dataset);

		console.log('Currdataset after assignment', Array.isArray(currDataset.data))

		currDataset.data = currDataset.data.map(el => { return {x: new Date(el.x), y: el.y} })

		console.log('The error is here', currDataset)

		let xData = currDataset.data.map(el => el.x);
		let yData = currDataset.data.map(el => parseInt(el.y));

		let lineType = currDataset.lineType ? currDataset.lineType : d3.curveLinear;
		let trendLine = currDataset.trendLine ? currDataset.trendLine : false;

		let xScale = d3.scaleTime().domain([xData[0], xData[xData.length - 1]]).range([chart.drawingBounds.left, chart.drawingBounds.right]);
		let yScale = d3.scaleLinear().domain([0, Math.max(...yData)]).range([chart.drawingBounds.bottom, chart.drawingBounds.top]);

		currDataset.modified = !Object.prototype.hasOwnProperty.call(currDataset, 'modified') ? false : true;
		currDataset.xData = xData;
		currDataset.yData = yData;
		currDataset.lineType = lineType;
		currDataset.trendLine = trendLine;
		currDataset.xScale = xScale;
		currDataset.yScale = yScale;
		currDataset.labelVal = yData[yData.length - 1]

		currDataset.store = yData.join("")

		return currDataset

	}

	function buildNewDataset (dataset, chart) {

		let currDataset = dataset;

		let xData = currDataset.data.map(el => el.x);
		let yData = currDataset.data.map(el => parseInt(el.y));

		let lineType = currDataset.lineType ? currDataset.lineType : d3.curveLinear;
		let trendLine = currDataset.trendLine ? currDataset.trendLine : false;

		let xScale = d3.scaleTime().domain([xData[0], xData[xData.length - 1]]).range([chart.drawingBounds.left, chart.drawingBounds.right]);
		let yScale = d3.scaleLinear().domain([0, Math.max(...yData)]).range([chart.drawingBounds.bottom, chart.drawingBounds.top]);

		currDataset.modified = !Object.prototype.hasOwnProperty.call(currDataset, 'modified') ? false : true;
		currDataset.xData = xData;
		currDataset.yData = yData;
		currDataset.lineType = lineType;
		currDataset.trendLine = trendLine;
		currDataset.xScale = xScale;
		currDataset.yScale = yScale;
		currDataset.labelVal = yData[yData.length - 1]

		currDataset.store = yData.join("")

	}

	function compareAndSelectDatasetByDomain (chart) {
		let memoryX = 0, memoryY = 0, winner = { x:0, y:0 }, index = 0, dataset = chart.dataset;
		
		if (chart.dataset.length > 0) {
			for (index; index < dataset.length; index++) {

				let currYMax = Math.max(...dataset[index].yData);
				let currXMax = dataset[index].xData.length;

				if (currXMax > memoryX) {
					memoryX = currXMax;
					winner.x = index;
				}
				if (currYMax > memoryY) {
					memoryY = currYMax;
					winner.y = index;
				}

			}
		}

		return winner;
	}
	
	function makeSVGRefac (chart) {
		let svg = chart.container.append('svg')
		.attr('width', chart.width)
		.attr('height', chart.height)
		.attr('id', chart.id)
		.append('g')
		.on('mouseleave', () => {

			for (let i = 0; i < chart.dataset.length; i++) {
				setLabelVal(chart, i, chart.dataset[i].data.length - 1)
			}

		});

		return svg;
	}

	function makeSVG (chart) {
		let svg = chart.container.append('svg')
		.attr('width', chart.width)
		.attr('height', chart.height)
		.attr('id', chart.id)
		.append('g')
		.on('mouseleave', () => {

			for (let i = 0; i < chart.dataset.length; i++) {
				setLabelVal(chart, i, chart.dataset[i].data.length - 1)
			}

		});

		chart.svg = svg;
	}

	function drawYAxisRefac (chart, offset=0) {
		let yAxis = chart.svg.append('g')
		.attr('id', chart.id + '-y-axis')
		.attr('class', 'axis')
		.attr('transform', `translate(${chart.drawingBounds.left + offset}, 0)`)
		.call(d3.axisLeft(chart.yScale))
		return yAxis;
	}

	function drawYAxis (chart, offset=0) {
		let yAxis = chart.svg.append('g')
		.attr('id', chart.id + '-y-axis')
		.attr('class', 'axis')
		.attr('transform', `translate(${chart.drawingBounds.left + offset}, 0)`)
		.call(d3.axisLeft(chart.yScale))
		chart.yAxis = yAxis;
	}
	
	function drawXAxisRefac (chart) {
		let xAxis = chart.svg.append('g')
		.attr('id', chart.id + '-x-axis')
		.attr('class', 'axis')
		.attr('transform', `translate(0, ${chart.drawingBounds.bottom})`)
		.call(d3.axisBottom(chart.xScale))
		return xAxis;
	}

	function drawXAxis (chart) {
		let xAxis = chart.svg.append('g')
		.attr('id', chart.id + '-x-axis')
		.attr('class', 'axis')
		.attr('transform', `translate(0, ${chart.drawingBounds.bottom})`)
		.call(d3.axisBottom(chart.xScale))
		chart.xAxis = xAxis;
	}

	function drawHelperLines(chart, dataset_index, point_index) {
		let currDataset = chart.dataset[dataset_index];
		let currPoints = chart.dataset[dataset_index].points;
		if (currPoints) {
			point_index = point_index > currDataset.xData.length - 1 ? currDataset.xData.length - 1 : point_index;
			let currPoint = d3.select(currPoints.nodes()[point_index])
			let xPos = currPoint.attr('cx');
			let yPos = currPoint.attr('cy');
			chart.dataset[dataset_index].helperLine = chart.svg.append('polyline')
						.attr('points', `${xPos},${chart.drawingBounds.bottom} ${xPos},${yPos} ${chart.drawingBounds.left},${yPos}`)
						.style('fill', 'none')
						.style('stroke', 'red')
						.style('stroke-dasharray', '3');
		}
	}	

	function removeHelperLines(chart, dataset_index) {
		let currDataset = chart.dataset[dataset_index];
		if (currDataset.helperLine) {
			d3.select(currDataset.helperLine.node()).remove()
			currDataset.helperLine = null;
		}
	} 

	function drawBars (chart, dataset) {
		console.log('My Current dataset is :', dataset)

		let bars = chart.svg.append('g')
		.attr('id', dataset.label + '-bars')
		.selectAll('path')
		.data(dataset.data).enter()
		.append('path')
		.attr('d', d => 

			`M ${chart.xScale(d.x) - chart.barWidth} ${chart.drawingBounds.bottom}
			H ${chart.xScale(d.x) + chart.barWidth}
			V ${chart.yScale(d.y)}
			H ${chart.xScale(d.x) - chart.barWidth} Z`

		)
		.style('fill', dataset.color)
		.style('stroke', 'black')
		.attr('class', dataset.label + '-bar')

		return bars

	}

	function drawMouseGridRefac (chart, datasetId) {
		if (chart.dataset[datasetId]) {
			let grid = chart.svg.append('g');

			grid.attr('id', chart.id + '-mouseGrid')
			.selectAll('path')
			.data(chart.dataset[datasetId].data)
			.enter()
			.append('path')
			.attr('d', d => 

					`M ${chart.xScale(d.x) - chart.gridSpacing} 0
					H ${chart.xScale(d.x) + chart.gridSpacing}
					V ${chart.drawingBounds.bottom}
					H ${chart.xScale(d.x) - chart.gridSpacing} Z`
			
			)
			.style('pointer-events', 'all')
			//.style('stroke', 'black') --> DEGUB
			.attr('class', (d, i) => chart.id + '-mouseGrid-' + i)

			.on('mouseenter', (d, i) => {

				for (let index = 0; index < chart.dataset.length; index++) {
					setLabelVal(chart, index, i)
					drawHelperLines(chart, index, i)
					styleHoverPoint(chart, 'enter', index, i, {color: chart.dataset[index].color, radius: 4})
				} 

			})

			.on('mouseleave', (d, i) => {

				for (let index = 0; index < chart.dataset.length; index++) {
					styleHoverPoint(chart, 'leave', index, i, {color: chart.dataset[index].color, radius: 3})
					removeHelperLines(chart, index)
				}

			})

			setNoFill(grid);

			return grid;
		}

	}

	function drawMouseGrid (chart, datasetId) {
		if (chart.dataset[datasetId]) {
			let grid = chart.svg.append('g');

			grid.attr('id', chart.id + '-mouseGrid')
			.selectAll('path')
			.data(chart.dataset[datasetId].data)
			.enter()
			.append('path')
			.attr('d', d => 

					`M ${chart.xScale(d.x) - chart.gridSpacing} 0
					H ${chart.xScale(d.x) + chart.gridSpacing}
					V ${chart.drawingBounds.bottom}
					H ${chart.xScale(d.x) - chart.gridSpacing} Z`
			
			)
			.style('pointer-events', 'all')
			//.style('stroke', 'black') --> DEGUB
			.attr('class', (d, i) => chart.id + '-mouseGrid-' + i)

			.on('mouseenter', (d, i) => {

				for (let index = 0; index < chart.dataset.length; index++) {
					setLabelVal(chart, index, i)
					drawHelperLines(chart, index, i)
					styleHoverPoint(chart, 'enter', index, i, {color: chart.dataset[index].color, radius: 4})
				} 

			})

			.on('mouseleave', (d, i) => {

				for (let index = 0; index < chart.dataset.length; index++) {
					styleHoverPoint(chart, 'leave', index, i, {color: chart.dataset[index].color, radius: 3})
					removeHelperLines(chart, index)
				}

			})

			setNoFill(grid);

			chart.grid = grid;
		}

	}

	function deleteDatasets (chart, indices) {
		if (indices) {
			indices.sort((a, b) => a - b);
			console.log('Indices to Delete:', indices)
			for (let i = indices.length - 1; i >= 0; i--){
				let currDataset = chart.dataset[indices[i]];
				console.log('Erasing ' + currDataset.dataLabel)
				if (currDataset.line) {
					currDataset.line.select(function() {
						this.parentNode.remove();
					})
				}
				if (currDataset.trendline) {
					currDataset.trendline.select(function() {
						this.parentNode.remove();
					})
				}
				if (currDataset.points) {
					currDataset.points.select(function() {
						this.parentNode.remove();
					})
				}
				console.log('Index to delete ' + indices[i])
				currDataset = null;
				chart.dataset.splice(indices[i], 1);
				console.log('Data Deleted Succesfully');
				console.log(chart.dataset)
			}
		}
	}

	function updateDrawnElement (chart, dataset) {
		let currDataset = dataset;

		// Update Line
		if (currDataset.line) {
			currDataset.line.transition().duration(700)
			.attr('d', chart.lineGen(currDataset.data));
		}
		
		// Update Trendline
		if (currDataset.trendline){
			currDataset.trendline.transition().duration(700)
			.attr('d', chart.trendGen(currDataset.data));
		}
		
		// Upadte Points
		if (currDataset.points){
			currDataset.points.data(currDataset.data).transition().duration(700)
			.attr('cy', d => chart.yScale(d.y))
			.attr('cx', d => chart.xScale(d.x));
		}
		
		console.log('Data modified succesfully')	
	}

	function checkStatus (dataset) {
		let store = dataset.store ? dataset.store : false;
		let data = dataset.data ? dataset.data: false;
		let dataId = dataset.yData ? dataset.yData.join("") : false;

		if (!data) {dataset.status = 'erased'; return 'erased'}
		else if (data && store && store != dataId) {dataset.status = 'modified'; return 'modified';}
		else if (dataset.data && dataset.store) {dataset.status = 'stable'; return 'stable';}
		else if (dataset.data && !dataset.store) {dataset.status = 'new'; return 'new'}
		else {dataset.status = 'erased'; return 'erased';}
	}

	return {
		months,
		formatDate,
		filterNationalData,
		filterRegionalData,
		defineBounds,
		createZeroValues,
		setNoFill,
		setStrokeColor,
		setStrokeWidth,
		setDashArray,
		transitionLine,
		transitionTrendline,
		transitionPoints,
		drawLine,
		drawTrendline,
		drawPoints,
		drawHelperLines,
		createGridSpacing,
		createLineGen,
		removeHelperLines,
		setLabelVal,
		styleHoverPoint,
		buildNewDataset,
		buildNewDatasetRefac,
		compareAndSelectDatasetByDomain,
		makeSVG,
		makeSVGRefac,
		drawYAxis,
		drawYAxisRefac,
		drawXAxis,
		drawXAxisRefac,
		drawBars,
		drawMouseGrid,
		drawMouseGridRefac,
		deleteDatasets,
		updateDrawnElement,
		checkStatus,
	}
})()