<script>
import axios from 'axios'

export default {

    name: 'MainPage',

    components: {

    },

    data: function( ) {
        
        return {

            mainCounter: '000',
            mainCounterDelta: '000',
            newCounter: '000',
            newCounterDelta: '000',
            recoveredCounter: '000',
            recoveredCounterDelta: '000',
            deadCounter: '000',
            deadCounterDelta: '000',
            ventilatorCounter: '000',
            dailyTestingCounter: '000',
            uciPatientsCounter: '000',
            tasaCounter: '000',
            criticalCounter: '000',
            dupDaysCounter: '0.0',
            dateUpdated: '24 de Mayo',
        
        }
    
    },
    
    created: function() {

        axios.get( '/api/main/' ).then( ( response ) => {

            let data = response.data

            this.dateUpdated = data.latest.date
            this.mainCounter = data.totals.count
            this.mainCounterDelta = data.totals.delta
            this.newCounter = data.new.count
            this.newCounterDelta = data.new.delta
            this.recoveredCounter = data.recovered.count
            this.recoveredCounterDelta = data.recovered.delta
            this.deadCounter = data.dead.count
            this.deadCounterDelta = data.dead.delta
            this.ventilatorCounter = data.ventilators.count
            this.dailyTestingCounter = data.pcr.count
            this.uciPatientsCounter = data.uci.count
            this.tasaCounter = data.tasa.count
            this.criticalCounter = data.critical.count
            this.dupDaysCounter = data.dupDays.count
        
        })
    
    }
   
}

</script>
