export {ChartMaker};

import {helpers} from './helpers.js'

const d3 = require('d3')

let _window = window,
    _doc = document,
    _el = _doc.documentElement,
    _body = _doc.getElementsByTagName('body')[0],
    _winWidth = _window.innerWidth || _el.clientWidth || _body.clientWidth;
   // _winHeight = _window.innerHeight|| _el.clientHeight|| _body.clientHeight;

const ChartMaker = (function(){

	function buildChart(options) {

		let chart = {}

		chart.uniqueId = options.uniqueId ? options.uniqueId : `${Math.round(Math.random(0, 255), 2)}`;
		chart.id = 'chart-' + chart.uniqueId;
		chart.container = options.container ? options.container : 'body'
		chart.width = options.width ? options.width : 300;
		chart.height = options.height ? options.height : 300;
		chart.dataset = options.dataset ? options.dataset : [{}];
		chart.padding = options.padding ? options.padding : [20, 20, 20, 40]

		// Handle resizing for different media
		let ratio = chart.width / chart.height;
		if (chart.width > _winWidth) {
			console.log('chart is wider')
			chart.width = _winWidth;
			chart.height = _winWidth/ratio * 2;	
		}

		chart.drawingBounds = helpers.defineBounds(chart.width, chart.height, chart.padding);

		for (let index = 0; index < chart.dataset.length; index++) {

			helpers.buildNewDataset(chart.dataset[index], chart);

		}

		let winner = helpers.compareAndSelectDatasetByDomain(chart);

		chart.gridSpacing = helpers.createGridSpacing(chart, winner.x);

		//Define our main xScale and yScale
		chart.yScale = chart.dataset[winner.y].yScale;
		chart.xScale = chart.dataset[winner.x].xScale;
		chart.defaultXScale = chart.xScale;
		chart.defautlYScale = chart.yScale;

		chart.lineGen = helpers.createLineGen(d3.curveLinear, chart); 
		chart.trendGen = helpers.createLineGen(d3.curveBundle.beta(0.3), chart);

		chart.currX = helpers.formatDate(chart.dataset[winner.x].xData[chart.dataset[winner.x].xData.length - 1], helpers.months);
		chart.winnerX = 0;
		chart.winnerY = 0;

		return Object.assign({}, chart);

	}
/*eslint-disable no-unused-vars*/	
	function drawChart(chart) {

		//Rebuild chart container selection - necessary given Vue's lifecycle:
		chart.container = d3.select(chart.container);

		helpers.makeSVG(chart);

		for (let i = 0; i < chart.dataset.length; i++) {
			helpers.drawLine(chart, chart.dataset[i]);
			helpers.drawPoints(chart, chart.dataset[i]);
			helpers.drawTrendline(chart, chart.dataset[i]);
		}
		
		//drawLines(chart);
		
		helpers.drawYAxis(chart);
		helpers.drawXAxis(chart);
		helpers.drawMouseGrid(chart, 0);

	}

	function updateChart(chart) {

		let indicesToDelete = [];

		for (let index = 0; index < chart.dataset.length; index++) {

			let currDataset = chart.dataset[index]
			let status = helpers.checkStatus(currDataset);

			if (status == 'new' || status == 'modified') {
				helpers.buildNewDataset(currDataset, chart);
			}

			if (currDataset.status == 'erased') {
				indicesToDelete.push(chart.dataset.indexOf(currDataset));
			}

		}

		// Erase marked datasets.

		helpers.deleteDatasets(chart, indicesToDelete);	

		// Re-calculate xScale, yScale, and linegens.

		let winner = helpers.compareAndSelectDatasetByDomain(chart)

		chart.winnerX = winner.x;
		chart.winnerY = winner.y;

		chart.yScale = chart.dataset.length > 0 ? chart.dataset[winner.y].yScale : chart.defaultYScale;
		chart.xScale = chart.dataset.length > 0 ? chart.dataset[winner.x].xScale : chart.defautlXScale;		
		chart.lineGen = d3.line().curve(d3.curveLinear).x(d => chart.xScale(d.x)).y(d => chart.yScale(d.y));
		chart.trendGen = d3.line().curve(d3.curveBundle.beta(0.3)).x(d => chart.xScale(d.x)).y(d => chart.yScale(d.y));

		let adjustedGrid = helpers.createGridSpacing(chart, winner.x);
		chart.gridSpacing = adjustedGrid ? adjustedGrid : chart.gridSpacing;
		
		chart.grid.remove();
		helpers.drawMouseGrid(chart, winner.x);

				
		// Draw new Axes
		let xAxis = d3.axisBottom(chart.xScale)
		let yAxis = d3.axisLeft(chart.yScale)

		if (chart.dataset.length > 0){
			chart.xAxis.transition().duration(700).call(xAxis);
			chart.yAxis.transition().duration(700).call(yAxis);
		}

		for (let index = 0; index < chart.dataset.length; index++) {

			let currDataset = chart.dataset[index];

			if (currDataset.status == 'modified' || currDataset.status == 'stable') {

				helpers.updateDrawnElement(chart, currDataset);

			}

			if (currDataset.status == 'new') {

				helpers.drawLine(chart, currDataset);
				helpers.drawTrendline(chart, currDataset);
				helpers.drawPoints(chart, currDataset);
				console.log('Data added succesfully')

			}

		}

	}
/*eslint-enable no-unused-vars*/
	return {
			buildChart,
			drawChart,
			updateChart,
	}

})();