<template>
  <div id="chartWrapper" ref="chartWrapper" class="container chartWrapper">
    <div class="row sectionTitleRow">
      <div class="col sectionTitleCol">
        <hr class="sectionDivider">
        <h3 class="sectionTitle">COMPARAR DATOS</h3>
      </div>
    </div>

    <div class="row selectorRow">
      <div class="col modeSelectorCol">

        <!-- MODE CONTROLLER -->

        <b-button-group size="sm">
          <b-button v-on:click="changeMode(0)" class="btn btn-light" v-bind:class="{active: mode == 0}">Totales</b-button>
          <b-button v-on:click="changeMode(1)" class="btn btn-light" v-bind:class="{active: mode == 1}">Nuevos</b-button>
        </b-button-group>
      </div>
      <div class="col dropDownCol">

      <!-- DROPDOWN --> 

        <b-dropdown id="regionDropdown" size="sm" no-flip right boundary="window" text="Regiones">
          <b-dropdown-item-button v-for="key in dropDownList" v-bind:key="key" v-on:click="modify(key)" v-bind:active="controller[key].active">{{ controller[key].label }}</b-dropdown-item-button>
        </b-dropdown>
      </div>
    </div>
    <div class="row justify-content-start">
      <div class="col">
        
      </div>
    </div>
    <div class="row chartAndLegendRow">
      <div class="col">
        <div id="chart" width='500' height='400'></div>
      </div>

    <!-- CHART LEGEND -->

      <div class="col legend">
        <b-list-group class="legendGroup">
          <b-list-group-item class="d-flex justify-content-between align-items-center legendItem">
            <p class="legendHeaderLabel">Fecha:</p><p v-if="data_ready" class="legendHeaderDate">{{ myChart.currX }}</p>
          </b-list-group-item>
          <b-list-group-item v-for="dataset in myChart.dataset" v-bind:key="dataset.dataLabel" class="d-flex justify-content-between align-items-center legendItem">  
            <b-icon-x-circle v-on:click="modify(dataset.controllerLabel)"></b-icon-x-circle>{{ dataset.label }}
            <b-badge variant="primary" pill v-bind:style="`background-color:${dataset.color}`"> {{ dataset.labelVal }} </b-badge>
          </b-list-group-item>
        </b-list-group>
      </div>

    </div>
  
  </div>
</template>

<script>
import axios from 'axios'
import { ChartMaker } from '../utils/ChartMaker.js'
import { BIconXCircle } from 'bootstrap-vue'
import { controller, regionMap } from '../utils/store.js'
import { helpers } from '../utils/helpers.js'

export default {

  name: 'MainChart',

  components: {

    BIconXCircle,

  },

  data: function() {
    
    return {

      regionalData: {},
      nationalData: {},
      regionMap: regionMap,
      data_ready: false,
      myChart: {currX: '---'},
      mode: 0,
      controller: controller,

    }

  },

  computed: {

    dropDownList: function() {

      if ( this.mode == 0 ) {

        return Object.keys(this.controller).filter(i => this.controller[i].mode.includes('total'))
      
      } else {

        return Object.keys(this.controller).filter(i => this.controller[i].mode.includes('new'))
      
      }

    },

  },

  created: function() {

    axios.get('/api/regional/').then((response) => {

      this.regionalData = response.data;

      axios.get('/api/totals/').then((response) => {

        this.nationalData = response.data;

        this.myChart = ChartMaker.buildChart( {

          uniqueId: '12',
          container: '#chart',
          width: 800,
          height: 300,
          dataset: [ {

            dataLabel: this.controller[ 'totales' ].dataLabel,
            data: helpers.filterNationalData( this.nationalData, 'totales' ),
            label: this.controller[ 'totales' ].label,
            controllerLabel: this.controller[ 'totales' ].controllerLabel,
            pointsOn: true,
            trendline: true,
            color: this.controller[ 'totales' ].color,
            
            } ]
        } );

        this.controller[ 'totales' ].active = true;

        this.$nextTick( () => {

          ChartMaker.drawChart(this.myChart)
        
        } )
 
        this.data_ready = true;

      } )

    } );

  },

  mounted: function() {

  },

  /*eslint-disable no-unused-vars*/
  methods: {

    changeMode: function( mode ) {

      if ( this.mode !== mode ) {

        let dataToReset = [];

        let keys = Object.getOwnPropertyNames( this.controller );
        keys.pop();
        
        for ( let i = 0; i < keys.length; i++ ) {

          if ( this.controller[keys[i]].active ) {

            dataToReset.push( this.controller[ keys[ i ] ].dataLabel );

            this.controller[ keys[ i ] ].active = false;
          
          }

        }

        for ( let dataset of this.myChart.dataset ) {
          
          if ( dataToReset.includes( dataset.dataLabel ) ) {
            
            dataset.data = null;
          
          }
        
        }

        this.mode = mode;
        
        ChartMaker.updateChart(this.myChart);
      
      }
        
    },
    
    modify: function( controllerKey ) {
      
      let datasetKeys = Array.prototype.map.call( this.myChart.dataset, el => el.dataLabel );

      if ( !datasetKeys.includes( this.controller[ controllerKey ].dataLabel ) ) {
        
        if ( this.regionMap.includes( controllerKey ) ) {
          
          this.controller[ controllerKey ].active = true;
          
          this.myChart.dataset.push( {

            dataLabel: this.controller[controllerKey].dataLabel,
            data: this.mode == 0 ? helpers.filterRegionalData( this.regionalData.regions, this.controller[ controllerKey ].dataLabel, 'confCases') : helpers.filterRegionalData( this.regionalData.regions, this.controller[ controllerKey ].dataLabel, 'newCases'), 
            label: this.controller[ controllerKey ].label,
            controllerLabel: this.controller[ controllerKey ].controllerLabel,
            pointsOn: true,
            trendline: true,
            color: this.controller[ controllerKey ].color,
          
          } )

        } else {
          
          this.controller[ controllerKey ].active = true;
          
          this.myChart.dataset.push( {
            
            dataLabel: this.controller[controllerKey].dataLabel,
            data: helpers.filterNationalData(this.nationalData, controllerKey),
            label: this.controller[controllerKey].label,
            controllerLabel: this.controller[controllerKey].controllerLabel,
            pointsOn: true,
            trendline: true,
            color: this.controller[controllerKey].color,
          
          } )

        }

        ChartMaker.updateChart(this.myChart);

      } else if ( datasetKeys.includes( this.controller[ controllerKey ].dataLabel ) ) {
        
        this.controller[ controllerKey ].active = false;
        let indexToRemove = datasetKeys.indexOf( this.controller[ controllerKey ].dataLabel );
        this.myChart.dataset[ indexToRemove ].data = null;
        ChartMaker.updateChart( this.myChart );
      
      }
    
    },

    formatDate: helpers.formatDate,

  }

}

</script>

